import {
  doCreateAccountSuccess,
  doCreateAccountStarted,
  doCreateAccountError,
} from "./create-account";
import Services from "../../services";

export const createAccount = (accountData) => {
  return function doCreateAccountAction(dispatch) {
    dispatch(doCreateAccountStarted());
    return Services.APICallConfig.callAPIService("CREATE_ACCOUNT", accountData)
      .then((response) => {
        dispatch(doCreateAccountSuccess(response));
      })
      .catch((error) => {
        console.log("error response", error);
        dispatch(doCreateAccountError(error));
      });
  };
};
